.wea-hrmlist {
  position: absolute;
  z-index: 101;
  background-color: #fff !important;
  border: 1px solid #19abec !important;
  max-height: 196px;
  width: 250px;
  -webkit-box-shadow: 0 0 4px #2db7f5;
          box-shadow: 0 0 4px #2db7f5;
  padding-bottom: 1px;
  font-size: 12px !important;
}
.wea-hrmlist .wea-new-scroll {
  position: relative;
  z-index: 1000;
  padding-bottom: 1px;
}
.wea-hrmlist .close-btn {
  font-size: 24px;
  position: absolute;
  top: -12px;
  right: -12px;
  color: #aaa;
  background-color: #fff !important;
  z-index: 1001;
  border-radius: 12px;
}
.wea-hrmlist li {
  border-bottom: 1px solid #d2d2d2 !important;
  padding: 5px 0;
  margin: 0 8px;
  color: #868686;
  font-size: 12px;
}
.wea-hrmlist li span {
  display: inline-block;
}
.wea-hrmlist li .name {
  font-size: 12px !important;
  width: 80px;
  color: #222;
  word-wrap: normal !important;
}
.wea-hrmlist li .jobname {
  font-size: 12px !important;
  padding-left: 10px;
  width: 150px;
  word-wrap: normal !important;
}
.hrmlist-wrapper .ant-popover-inner {
  border: none;
}
.hrmlist-wrapper .ant-popover-arrow {
  display: none;
}
.hrmlist-wrapper .ant-popover-inner-content {
  padding: 0;
}



