.wea-wf-input{
    position: relative;
}
.wf-input{
    min-height: 26px;
    font-size: 12px;
    color: #000;
    border: 1px solid #d9d9d9;
}
.wf-input:hover,.wf-input:focus{
    outline: none;
    border-color: #57c5f7;
}
.wf-input-main{
    width: 100%;
}
.wf-input-detail{
    width: 100%;
}
.wf-input-required{
    position: absolute;
    top: 50%;
    margin-top: -10px;
    line-height: 16px;
    padding-left: 2px;
    font-size: 12px !important;
}
.wf-input-chinese{
    color: #0f0f0f;
    margin-top: -1px;
}

.wea-wf-input .ant-input {
    min-height: 30px;
}

.wf-readonly-span {
    display: inline-block;
    margin-top: 5px;
}


