.wea-textarea-normal {
    position: relative;
    font-size: 12px;
    color: #000;
    display: inline-block;
    width: 100%;
}

.wea-textarea-normal .ant-input{
	border-radius: 0px;
    color: inherit;
    height: inherit;
    font-size: inherit;
}

.wea-textarea-normal.asterisk:after {
    top: 48%;
}

.wea-textarea-normal.wea-field-readonly {
    width: 100%;
    height: 100%;
    color: #666;
}



