.wea-browser.customized {
  display: inline-block;
}
.wea-workflow-radio-icon-circle {
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  border-radius: 50%;
  margin-right: 10px;
  color: #fff;
}
.wea-type-list {
  position: relative;
}
.wea-type-list .icon-coms-Flow-setting {
  position: absolute;
  right: 15px;
  top: 10px;
  color: #B2B2B2;
  font-size: 16px;
}
.wea-type-list-panel {
  position: fixed;
  background: #fff;
  z-index: 9999;
  border: 1px solid #e2e2e2;
  width: 500px;
}
.wea-type-list-panel .list-item,
.wea-type-list-panel .splite {
  display: inline-block;
}
.wea-type-list-panel .wea-type-list-header {
  padding: 10px 30px 0 15px;
  background: #FAFAFA;
  border-bottom: 1px solid #e2e2e2;
}
.wea-type-list-panel .wea-type-list-header .list-item {
  margin-bottom: 8px;
  margin-right: 20px;
  color: #4D7AD8;
}
.wea-type-list-panel .wea-type-list-header .list-item.active {
  color: #00A9FF;
  text-decoration: underline;
}
.wea-type-list-panel .wea-type-list-content {
  padding: 0 0 0 15px;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item {
  padding-right: 20px;
  margin-bottom: 10px;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item:first-child {
  margin-top: 15px;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item .wea-type-list-content-item-left {
  color: #999;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item .list-item {
  color: #333333;
  margin-bottom: 4px;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item .list-item:hover {
  color: #4D7AD8;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item .list-item.active {
  color: #00A9FF;
}
.wea-type-list-panel .wea-type-list-content .wea-type-list-content-item .splite {
  color: #555;
  margin: 0 5px;
}
.wea-browser-modal .wea-search-group .wea-form-item-wrapper {
  display: inline-block;
}



