.wf-findiv {
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.wf-findiv .fintop {
  height: 50%;
  border-bottom: solid 1px black;
}
.wf-findiv .finbottom {
  height: 50%;
}
.wf-findiv .fintable {
  height: 100%;
  float: right;
  border: 0px;
  border-spacing: 0px !important;
}
.wf-findiv .fintable .finborder {
  min-width: 18px;
}
.wf-findiv .fintable .fborder1 {
  border-right: solid 1px black;
}
.wf-findiv .fintable .fborder2 {
  border-right: solid 2px red;
}
.wf-findiv .fintable .fborder3 {
  border-right: solid 2px blue;
}



