.wea-error {
  position: relative;
}
.wea-error .error-tip {
  position: absolute;
  top: -31px;
  left: 0px;
  display: inline-block;
  padding: 1px 7px;
  line-height: 24px;
  z-index: 99;
  font-size: 12px;
  background-color: #ffebec;
  border-radius: 3px;
  border: 1px solid #ff3e3c;
  color: #666;
}
.wea-error .error-tip.tip-bottom {
  top: auto;
  bottom: -32px;
}
.wea-error .error-tip .anticon-exclamation-circle {
  color: #FF3F3F;
  font-size: 16px;
  position: absolute;
  top: 6px;
}
.wea-error .error-tip .tip-msg {
  margin-left: 24px;
}
.wea-error.has-error .ant-select-selection,
.wea-error.has-error .wea-associative-search,
.wea-error.has-error .ant-select-selection:hover,
.wea-error.has-error .ant-select-selection:focus,
.wea-error.has-error input.ant-input:hover,
.wea-error.has-error .wea-associative-search:hover,
.wea-error.has-error input.ant-input:focus,
.wea-error.has-error input.ant-input {
  border-color: #ff3e3c !important;
}



