.wea-date-picker {
  display: inline-block;
  position: relative;
  background-color: transparent;
}
.wea-date-picker .ant-calendar-picker {
  font-size: inherit;
  width: 100%;
  background-color: transparent;
}
.wea-date-picker.required {
  margin-right: 12px;
}
.wea-date-picker.noInput.required {
  margin-right: 7px;
}
.wea-date-picker.noInput.required:after {
  right: -6px;
}
.wea-date-picker.noInput .picker-icon {
  font-size: 16px;
  color: #2db7f5;
  left: 0;
  top: 50%;
  margin-top: -5px;
}
.wea-date-picker.noInput .text {
  padding-left: 20px;
  display: inline-block;
  width: 100%;
}
.wea-date-picker .ant-input,
.wea-date-picker .ant-time-picker-input,
.wea-date-picker .ant-calendar-range-picker {
  height: auto;
  min-height: 30px;
  font-size: inherit;
  color: inherit;
}
.wea-date-picker .ant-input,
.wea-date-picker .ant-time-picker-input {
  border-radius: 0px;
}
.wea-date-picker .ant-time-picker {
  font-size: inherit;
  width: 100%;
  background-color: transparent;
}
.wea-date-picker .ant-time-picker-input {
  width: 100%;
  padding-right: 26px;
}
.wea-date-picker .ant-calendar-picker-clear {
  z-index: 1;
}
.wea-date-picker .ant-calendar-picker-clear:hover {
  z-index: 1;
  opacity: 1;
}
.wea-date-picker-arrow {
  display: inline-block;
  -moz-user-select: none;
  /*火狐*/
  -webkit-user-select: none;
  /*webkit浏览器*/
  -ms-user-select: none;
  /*IE10*/
  /*早期浏览器*/
  user-select: none;
}
.wea-date-picker-arrow .label {
  cursor: pointer;
  padding: 0 10px;
}
.wea-date-picker-arrow .icon-coms-Browse-box-delete,
.wea-date-picker-arrow .icon-coms-Browse-box-Add-to {
  position: relative;
  top: 3px;
  color: #bababa;
  font-size: 16px;
  cursor: pointer;
}
.wea-year-picker .ant-calendar-year-panel {
  position: static;
}
.wea-time-panel-item {
  color: #000;
}
.wea-time-panel-item:hover {
  background: #edfaff;
}
.wea-time-panel-item.wea-time-panel-item-selected:hover {
  background: #67AFF7;
}
.wea-time-panel-item-selected {
  color: #fff;
  background: #67AFF7;
}



