.wea-tree {
  padding: 0;
}
.wea-tree .wea-tree-wrap:hover {
  background-color: #E9F7FF;
}
.wea-tree .wea-tree-wrap.wea-tree-wrap-true {
  background-color: #E9F7FF;
}
.wea-tree.ant-tree li {
  margin: 0;
}
.wea-tree.ant-tree li .ant-tree-switcher {
  color: #a3a3a3;
}
.wea-tree.ant-tree li ul {
  padding: 0;
}
.wea-tree.ant-tree li a {
  width: 100%;
  color: #333;
  padding: 1px 5px 1px 0;
}
.wea-tree.ant-tree li a.ant-tree-node-selected {
  background-color: transparent;
}
.wea-tree.ant-tree li a:hover {
  background-color: transparent;
}
.wea-tree .ant-radio-wrapper {
  margin-right: 2px;
}
.wea-tree .wea-tree-node-wrapper {
  position: relative;
}
.wea-tree .wea-tree-node-wrapper > div {
  display: inline-block;
  max-width: 100%;
  vertical-align: bottom;
}
.wea-tree .wea-tree-node-wrapper:hover {
  color: #018efb;
}
.wea-tree .wea-tree-node-wrapper:hover .ops-wrapper {
  display: inline-block;
  color: initial;
}
.wea-tree .wea-tree-node-wrapper .ops-wrapper {
  margin-left: 50px;
  display: none;
  height: 18px;
  line-height: 18px;
  font-size: 12px;
}
.wea-tree .wea-tree-node-wrapper .ops-wrapper .ops-icon:not(:last-child) {
  margin-right: 8px;
}
.wea-tree-strictlybtn {
  position: absolute;
  right: 30px;
  top: 15px;
  z-index: 1;
}



