.wea-city-card-container {
  width: 478px;
  background-color: #fff;
  position: absolute;
  z-index: 1050;
  top: 30px;
}
.wea-city-card-container .spantag {
  display: inline-block;
  color: #343434;
  padding: 3px 10px;
  font-size: 12px;
  border-radius: 3px;
}
.wea-city-card-container .spantag:hover {
  color: #4bb1fd;
  background-color: #fff;
}
.wea-city-card-container .selected {
  background-color: #4bb1fd;
  color: #fff;
}
.wea-city-card-container .ant-tabs-content {
  padding-bottom: 10px;
}
.wea-city-card-container .ant-tabs-card {
  border: 1px solid #d9d9d9;
}
.wea-city-card-container .ant-tabs-nav-container {
  background-color: #fafafa;
}
.wea-city-card-container .ant-tabs.ant-tabs-card > .ant-tabs-bar {
  margin-bottom: 10px;
}
.wea-city-card-container .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
  margin-right: 0;
  border-radius: 0;
  border: 0;
  border-right: 1px solid #d9d9d9;
  border-bottom: 1px solid #cccccc;
}
.wea-city-card-container .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab:last-child {
  border-right: 0;
}
.wea-city-card-container .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
  border-bottom: transparent;
}
.wea-city-card-container .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab .ant-tabs-tab-inner {
  height: 36px;
  line-height: 36px;
  padding: 0 35px;
}
.wea-city-card-container .ant-col-4 {
  text-align: center;
}
.wea-city-picker .asterisk {
  margin-right: 15px;
}
.wea-city-picker-modal-mask {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
  height: 100%;
  z-index: 10;
}



